// import store from '@/store'
export default {
	data() {
		return {
			is_start_already:''
		};
	},
	onLoad() {
		this.init()
	},
	methods:{
		async init(){
			await this.$store.dispatch('Home/init');
			let _this = this
			let timer = setInterval(function(){
				if(_this.$store.getters.is_start_already){
					uni.reLaunch({//信息更新成功后跳转到小程序首页
						url: '/pages/home/home'
					});
					clearInterval(timer)
				}
			},1000)
			
		},
	}
}
